<template>
  <el-row :gutter="0" :style="'height:'+bannerHeight+'px;'">
    <el-col :span="24">
      <Carousel
        :direction="direction"
        directionMode="hover"
        :directionSize="directionSize"
        directionColor="skyblue"
        :indicator="indicator"
        indicatorMode="always"
        indicatorColor="white"
        indicatorActiveColor="skyblue"
        @before-moving="beforeMoving"
        @after-moving="afterMoving"
      >
        <CarouselItem v-for="(item, index) in store.state.bannerPicList" :key="index" :idx="index">
          <div :style="'position:relative;height:100%; background:url('+item.url+') no-repeat center center; background-size:cover;'">
            <img v-for="(pic, index) in item.list" :key="index" :src="pic.url" alt="" :style="'position: absolute; top: '+pic.position.top+'; left: '+pic.position.left+'; width: '+pic.width+'; height: auto; animation-delay: '+pic.delay+'s;'" class="animation">
          </div>
        </CarouselItem>
      </Carousel>
    </el-col>
  </el-row>
</template>

<script setup>
import {onMounted, ref, defineProps} from 'vue'
import {useStore} from 'vuex'
const store = useStore()

let props = defineProps({
  bannerType: {
    type: Number,
    default: 1
  }
})
// 根据参数修改
let direction = ref(false)
let indicator = ref(false)
let directionSize = ref(0)
if (props.bannerType === 1) {
  direction.value = true
  indicator.value = true
  directionSize.value = 20
}
store.commit('changeBannerType', props.bannerType)

// 判断当前大小
let currentBanner = null;
const getBannerHeight = () => {
  let language = localStorage.getItem('language') || 'Cn'
  let w = document.body.clientWidth
  if (currentBanner === null) {
    // 初始化
    if (w > 992) {
      currentBanner = 1
    } else {
      currentBanner = 2
    }
    store.commit('changeBanner' + language)
  } else {
    // 减少数据变动，只有在大小改变时才执行
    if (w > 992) {
      if (currentBanner !== 1) {
        currentBanner = 1
        store.commit('changeBanner' + language)
      }
    } else {
      if (currentBanner !== 2) {
        currentBanner = 2
        store.commit('changeBanner' + language)
      }
    }
  }
  if (w > 1600) {
    return 695
  } else if (w > 992) {
    return 695 * w / 1600
  } else {
    return 800 * w / 992
  }
}
let bannerHeight = ref(getBannerHeight())
const beforeMoving = (dire) => {
  setTimeout(()=>{
    checkScroll()
  }, 0)
}
const afterMoving = (obj) => {
  setTimeout(()=>{
    checkScroll()
  }, 0)
}
onMounted(() => {
  window.addEventListener('resize', () => {
    bannerHeight.value = getBannerHeight()
  })
  checkScroll()
})
</script>

<style lang="scss">
.Carousel {
  .prev {
    left: 60px !important; height: 70px !important; width: 70px !important;
    border-radius: 50%;
    i {font-size: 30px !important; line-height: 70px !important;}
  }
  .next {
    right: 60px !important; height: 70px !important; width: 70px !important;
    border-radius: 50%;
    i {font-size: 30px !important; line-height: 70px !important;}
  }
}
</style>
